
.tehtudtood-container {
    background-color: white;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-top: 1rem;
    /* border-bottom: solid 1px rgb(205, 204, 204); */

}
.tehtudtood-background-img {
    background-image: url("https://raw.githubusercontent.com/rakanishudDev/tommyEhitusGrupp/main/imgs/whitestonepaint3.jpg");

}
.tehtudtood-row {
    background: linear-gradient(white, rgba(101, 101, 101, 0.05), white);

    padding: 0 2rem;
    padding-bottom: 2.5rem;
    border-left: 1px solid rgb(201, 205, 212);
 


}
.imgs-container {
    /* background-color: rgba(248, 248, 255, 0.7); */
    
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
}
.img-container {
    max-height: 202px;
    display: flex;
    overflow: hidden;
    height: fit-content;
    border: solid 1px rgb(162, 162, 162);
    box-shadow: 3px 3px 8px rgb(50, 50, 50);

}
.aadress {
    font-size: 1.1rem;
    font-weight: 500;
    margin-top: 0;
}
.img {
    width: 300px;
    object-fit: cover;
}
.img:hover {
    cursor: pointer;
}